




















import { Component, Prop, Vue } from "vue-property-decorator";
import { Tag } from "@/api/models/Tag";

@Component
export default class ProjectTag extends Vue {
    /**
     * Tag to display.
     */
    @Prop()
    tag: Tag;

    /**
     * Should the tag have padding.
     */
    @Prop({ default: true })
    showPadding: boolean;
}
